body {
  margin: 0;

  //   color: $primary-text-color;
  //   background-color: $primary-bg-color;

  //   @include font(400, 14px, 1.2, 0.03em);
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  font-style: normal;
}

button,
a {
  cursor: pointer;
}

input {
  //   border: 1px solid $basicWhiteColor;
  border-radius: 4px;
  //   transition: 250ms $primaryTransition;
  &:focus {
    // border: 1px solid $accentColor;
    outline: none;
  }
}
